.banner {
  width: 100%;
  padding: 24px;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
  height: 100%;
  min-height: 420px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.banner__image-box {
  position: relative;
  width: 100%;
  max-width: 298px;
  max-height: 250px;
  overflow: hidden;
}

.banner__image-box:before {
  content: "";
  display: block;
  padding-top: 100%;
  transition: .3s;
  position: relative;
}

.banner__image {
  position: absolute;
  top: 24px;
  left: 24px;
  bottom: 24px;
  right: 24px;
  margin: auto;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  max-width: 100%;
}

.banner__title {
  font-size: 24px / 30px;
  font-weight: 500;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner__caption {
  font-size: 16px / 24px;
  height: 48px;
  overflow: hidden;
  color: #fff;
  text-align: center;
  margin-bottom: 25px;
}

.banner__button {
  text-align: center;
  max-width: 216px;
  width: 100%;
  margin: auto;
}

@media (min-width: 576px) {
  .banner--long {
    min-height: auto;
    height: auto;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }
  .banner--long .banner__title {
    position: absolute;
    width: 190px;
    height: 100%;
    top: 0;
    left: 0;
  }
  .banner--long .banner__image-box {
    width: 190px;
    max-height: none;
    margin: 0;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -95px;
  }
  .banner--long .banner__bottom {
    width: calc(100% - 190px);
    height: 100%;
  }
  .banner--long .banner__image {
    background-size: contain;
  }
  .banner--big .banner__title {
    font-size: 60px;
  }
}

@media (min-width: 768px) {
  .banner--long .banner__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .banner--long .banner__caption {
    width: calc(100% - 216px);
    margin-bottom: 0;
    text-align: left;
    height: auto;
    max-height: 48px;
    overflow: hidden;
    padding-right: 32px;
  }
}

