.cp__wrapper {
  width: 250px;
  margin: 0;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.3);
  font-family: Menlo, 'Microsoft Yahei', sans-serif;
  user-select: none;
}

.cp__v-ctrl {
  position: relative;
}

.cp__thumb {
  position: absolute;
  width: 12px;
  height: 12px;
  top: 0;
  border-radius: 50%;
  margin-top: -1px;
  transform: translateX(-50%);
  background-color: #f8f8f8;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.368627);
  cursor: default;
}

.cp__saturation {
  position: relative;
  width: 100%;
  padding-bottom: 55%;
  border-radius: 2px 2px 0 0;
  overflow: hidden;
}

.cp__saturation > div {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
}

.cp__saturation > .msk-white {
  background: linear-gradient(90deg, #fff, rgba(255, 255, 255, 0));
}

.cp__saturation > .msk-black {
  background: linear-gradient(0deg, #000, transparent);
}

.cp__saturation > .cp__thumb {
  background-color: transparent;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3), 0 0 1px 2px rgba(0, 0, 0, 0.4);
}

.cp__ctrl-pane {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 16px 16px 12px;
}

.cp__ctrl-pane > div {
  display: flex;
  align-items: center;
}

.cp__tracks {
  flex: 1 0 0;
}

.cp__ctrl-bar {
  height: 10px;
}

.cp__ctrl-hue {
  background: linear-gradient(-90deg, red, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, red);
}

.cp__ctrl-alpha {
  margin-top: 8px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMUlEQVQ4T2NkYGAQYcAP3uCTZhw1gGGYhAGBZIA/nYDCgBDAm9BGDWAAJyRCgLaBCAAgXwixzAS0pgAAAABJRU5ErkJggg==) left center;
}

.cp__preview {
  position: relative;
  width: 28px;
  height: 28px;
  margin-right: 5px;
  overflow: hidden;
  border-radius: 50%;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMUlEQVQ4T2NkYGAQYcAP3uCTZhw1gGGYhAGBZIA/nYDCgBDAm9BGDWAAJyRCgLaBCAAgXwixzAS0pgAAAABJRU5ErkJggg==) center;
}

.cp__preview > div {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid transparent;
  box-sizing: border-box;
}

.cp__fm-fields {
  display: flex;
  flex: 1;
}

.cp__fm-fields > div {
  padding-left: 6px;
  flex: 1 0 0;
}

.cp__fm-fields input {
  width: 100%;
  height: 22px;
  font-size: 11px;
  text-align: center;
  color: #333333;
  border-radius: 2px;
  border: none;
  box-shadow: #dadada 0px 0px 0px 1px inset;
  transition: box-shadow 0.2s ease;
  -moz-appearance: textfield;
}

.cp__fm-fields input:focus {
  outline: 0;
  box-shadow: #007dff 0px 0px 0px 1px inset;
}

.cp__fm-fields input::-webkit-inner-spin-button, .cp__fm-fields input::-webkit-outer-spin-button {
  -webkit-appearance: none !important;
  margin: 0;
}

.cp__fm-fields span {
  display: block;
  margin-top: 12px;
  text-transform: uppercase;
  font-size: 11px;
  line-height: 11px;
  color: #969696;
  text-align: center;
}

/* color format switcher */
.cp__fm-switcher {
  position: relative;
  width: 32px;
  text-align: right;
}

.cp__fm-switcher > div {
  position: relative;
  margin-right: -4px;
  margin-top: 12px;
  cursor: pointer;
}

.cp__fm-switcher > div > svg {
  width: 24px;
  height: 24px;
  border-radius: 5px;
  background: transparent;
  border: 1px solid transparent;
}

.cp__fm-switcher > div > svg:hover {
  border-color: #eeeeee;
  background: #eeeeee;
}

.dz-icon {
  color: rgba(255, 255, 255, 0.4);
  font-size: 30px;
}

.dropzone .dz-preview {
  position: relative;
}

.dropzone .dz-preview .dz-success-mark, .dropzone .dz-preview .dz-error-mark {
  width: 54px;
  text-align: center;
}

.dropzone .dz-preview.dz-image-preview {
  background-color: transparent;
}

.dropzone .dz-preview .dz-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.dz-remove {
