  bottom: 100%;
  width: calc(100% + 40px);
  text-align: center;
  margin: 0 -20px 5px;
}

.dropzone .dz-message {
  font-size: 18px;
}

.edit-photo-card {
  position: relative;
  z-index: 1;
  background-color: #fff;
}

.edit-photo-card:hover {
  z-index: 2;
}

.edit-photo-card__preview {
  display: block;
  border-width: 1px;
  border-style: solid;
  border-color: #dbe1e8;
}

.edit-photo-card:hover .edit-photo-card__preview {
  border-color: var(--primary-color);
  border-bottom-color: transparent;
  transition: border-color .3s;
}

.edit-photo-card--deleted .edit-photo-card__image {
  opacity: .2;
}

.edit-photo-card__image {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.edit-photo-card__image::before {
  content: '';
  display: block;
  padding-top: 100%;
}

.edit-photo-card__controls {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 5px;
  border-width: 0 1px 1px 1px;
  border-color: #1bbae1;
  border-style: solid;
  border-radius: 0 0 5px 5px;
  opacity: 0;
  background-color: #fff;
  z-index: -1;
}

.edit-photo-card:hover .edit-photo-card__controls {
  opacity: 1;
  z-index: 1;
  transition: .3s;
}

.edit-photo-card__deleted-icon {
  position: absolute 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: var(--danger-color);
  transition: .3s;
  opacity: 0;
  z-index: -1;
}

.edit-photo-card--deleted .edit-photo-card__deleted-icon {
  opacity: 1;
  z-index: 1;
}

.edit-photo-card--has-error .edit-photo-card__preview {
  border-color: var(--error-color);
}

.edit-photo-card--has-error:hover .edit-photo-card__preview {
  border-color: var(--error-color);
  border-bottom-color: transparent;
}

.edit-photo-card--has-error .edit-photo-card__controls {
  border-color: var(--error-color);
}

.ui-sortable-helper .edit-photo-card__preview {
  border-bottom-color: var(--primary-color) !important;
}

.ui-sortable-helper .edit-photo-card__controls {
  display: none;
}

.ui-sortable-helper .edit-photo-card--has-error .edit-photo-card__preview {
  border-bottom-color: var(--error-color) !important;
}

