}

.btn-group > .btn {
  margin: 0;
}

.block-illuminated {
  border-color: #c2c8cf;
}

.form-bordered .form-group:last-child {
  border-bottom: none;
}

.btn-separator-xs {
  border-left: 1px solid #dbe1e8;
  margin: 0 8px;
  display: inline-block;
  vertical-align: middle;
  height: 18px;
}

.btn-separator-xs:first-child, .btn-separator-xs:last-child,
.btn-separator-xs + .btn-separator-xs {
  display: none;
}

textarea.form-control {
  min-height: 200px;
  resize: vertical;
}

.pagination > li {
  display: inline-block;
  vertical-align: bottom;
}

.pagination > li > a,
.pagination > li > span {
  width: 30px;
  padding: 6px 0;
  text-align: center;
}

.select2-selection__clear {
  height: 22px;
}

.dataTable thead th {
  white-space: nowrap;
}



